body {
  background-color: #282c34;
}

.App {
  text-align: left;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

  /* Center all content */
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.header-text {
  text-align: center;
}

.input-wrapper {
  border-radius: 5px;
  overflow: hidden;
}

.form-input {
  overflow: hidden;
  width: 80vw;
}

.text-input {
  font-size: 16px;
  width: 100%;
}

.metadata-container {
  width: 80vw;
}

.send-button {
  background-color: rgb(224, 84, 84);
  border: white solid 2px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  font-size: 25px;
  height: 40px;
  margin-top: 10px;
  width: 200px;
}

.copy-button {
  background-color: rgb(84, 152, 224);
  border: white solid 2px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  font-size: 25px;
  height: 40px;
  margin-top: 10px;
  width: 200px;
}
